import React from 'react'
import { Button, Modal, Cascader, Input, Icon } from 'antd'
import { getequipmentlist, deleteequipment } from '/api/equipment'
import { getworktree } from '/api/workshop'
import Common from '/page/common.jsx'

import EquipmentForm from '/page/equipment/equipmentForm.jsx'

class Equipment extends Common{

	async componentWillMount () {
		this.getWorkTree()
		this.search()
	}

	state = Object.assign({}, this.state, {
		search: Object.assign({}, this.state.pageconf),
		columns: [{
			title: '设备',
			dataIndex: 'name'
		},{
			title: '设备编号',
			dataIndex: 'code'
		},{
			title: '所在车间',
			dataIndex: 'workshopName'
		},{
			title: '消防系统',
			dataIndex: 'worksystemName'
		}],
		modalConf: {visible: false, item: {}},
		workTree: []
	})

	getWorkTree = _ => {
		return getworktree({}).then(res => {
			this.setState({workTree: res.data})
		})
	}

	onWorkChange = (val, opt) => {
		this.setState({search: Object.assign({}, this.state.search, {type: val.length-1,id: val[val.length-1]})})
	}
	filterWork = (inputValue, path) => {
	  return (path.some(option => (option.label).toLowerCase().indexOf(inputValue.toLowerCase()) > -1));
	}

	search = async _ => {
		await this.setState({loading: true, selected: {}})
		let search = Object.assign({}, this.state.search)
		return getequipmentlist(search)
		.then(res => {
			let data
			data = res.data.records.map(val => Object.assign({}, val, {key: val.id, worksystemIds: [val.workshopId, val.worksystemId]}))
			this.setState({
				tabledata: data, 
				loading: false,
				pagination: Object.assign({}, this.state.pagination, {
					total: res.data.total,
					current: res.data.current
				})
			})
		})
	}


	delete = _ => {
		this.handleOk(deleteequipment, 'id', '删除')
	}

	done = async _ => {
		let modalConf = {visible: false, item: {}}
		await this.setState({ modalConf })
		this.research()
	}

	renderSearch = _ => <div className="mgrSearchBar">
		<Input 
		value={this.state.search.name}
		suffix={this.state.search.name ? <Icon 
		type="close-circle" 
		onClick={_ => this.changeSearch({name: ''})} /> : null}
		onChange={e => this.changeSearch({name: e.target.value})}
		style={{width: 250, marginRight: 15}}
		addonBefore="名称" placeholder="名称" />
		<Cascader 
			placeholder='请选择' 
			options={this.state.workTree}
			onChange={this.onWorkChange}
			showSearch={this.filterWork}
			filedNames={{ label: 'name', value: 'id', children: 'children' }}
			style={{width: 250, marginRight: 15}} changeOnSelect />
		<Button 
		onClick={this.search}
		type="primary" icon="search">搜索</Button>
		<Button 
		onClick={this.research}
		type="primary" icon="retweet">重置</Button>
	</div>

	renderBtn = _ => <div className="mgrToolBar">
		<Button 
		onClick={_ => this.addmodal('modalConf', '添加')}
		type="primary" icon="plus">添加</Button>
		<Button 
		onClick={_ => this.editmodal('modalConf', '编辑')}
		type="primary" icon="edit">修改</Button>
		<Button 
		onClick={this.changemodel}
		type="primary" icon="delete">删除</Button>
	</div>

	rendermodal = _ => [<EquipmentForm 
		onCancel={_ => this.cancelform('modalConf')}
		done={this.done}
		worktree={this.state.workTree}
		config={this.state.modalConf} />,
		<Modal title="信息"
		  visible={this.state.visible}
		  onOk={this.delete}
		  mask={false}
		  width={400}
		  onCancel={this.changemodel}
		  okText="确认"
		  cancelText="取消"
		>
			<p>是否删除？</p>
		</Modal>]

}

export default Equipment


