import http from '/api'

export const getequipmentlist = (params = {}) =>
	http.fetchGet(`/equipment/list`, params)

export const deleteequipment = (params = {}) =>
	http.fetchGet(`/equipment/delete`, params)

export const addequipment = (params = {}) =>
	http.fetchPost(`/equipment/add`, params, true)

export const updateequipment = (params = {}) =>
	http.fetchPost(`/equipment/update`, params, true)

export const getallequipments = (params = {}) =>
	http.fetchPost(`/equipment/listAll`, params, true)


