import React, { Component } from 'react'
import { Modal, Input, Cascader, Form, message } from 'antd'
const FormItem = Form.Item
import { addequipment, updateequipment } from '/api/equipment'

class EquipmentForm extends Component{
	async componentWillReceiveProps (nextprops) {
		if (nextprops.config != this.props.config && nextprops.config.visible) {
			if (nextprops.config.type != 'edit') {
				this.props.form.resetFields()
			} else {
				let mes = nextprops.config.item
				let config = {}
				let fieldsList = ['name', 'code', 'worksystemIds']
				fieldsList.forEach(val => {
					config[val] = {value: mes[val]}
				})

				this.props.form.setFields(config)
			}
		}
	}
	state = {
		rules: [{
			label: '设备名称',
			key: 'name',
			option: {rules: [{required: true, message: '请输入名称!'},{max: 20, message: '最多输入20个字符'}]},
			render: _ => <Input style={{width: 200}}/>
		}
		,{
			label: '设备编号',
			key: 'code',
			option: {rules: [{max: 20, message: '最多输入20个字符'}]},
			render: _ => <Input style={{width: 200}}/>
		},{
			label: '消防系统',
			key: 'worksystemIds',
			option: {rules: [{required: true, message: '请选择消防系统'}]},
			render: _ => <Cascader 
			placeholder='请选择' 
			options={this.props.worktree.map(o => {
				if (o.children.length == 0){ o.disabled = true }
				return o
			})}
			showSearch={this.filterWork}
			filedNames={{ label: 'name', value: 'id', children: 'children' }}
			style={{width: 250, marginRight: 15}} />
		}],
		loading: false,
		lock: false
	}

	filterWork = (inputValue, path) => {
	  return (path.some(option => (option.label).toLowerCase().indexOf(inputValue.toLowerCase()) > -1));
	}

	handleFetch = params => {
		if (this.props.config.type == 'edit') {
			params.id = this.props.config.item.id
			return updateequipment(params)
		}
		return addequipment(params)
	}

	handleOk = async _ => {
		if (!this.state.lock) {
			await this.setState({lock: true})
			this.props.form.validateFieldsAndScroll(null, {}, (err, val) => {
				if (!err || !Object.getOwnPropertyNames(err).length) {
					let params = Object.assign({}, val)
					if (params.worksystemIds) {
						let length = params.worksystemIds.length
						params.worksystemId = params.worksystemIds[length-1]
					}
					this.handleFetch(params)
					.then(res => {
						if (res.code == 200 || res === true) {
							message.success('操作成功')
							this.props.done()
						}
						this.setState({lock: false})
					})
				} else {
					this.setState({lock: false})
				}
			})
		}
	}

	render = _ => {
		const { getFieldDecorator } = this.props.form
		const formItemLayout = {
			labelCol: {
				xs: { span: 24 },
				sm: { span: 6 },
			},
			wrapperCol: {
				xs: { span: 24 },
				sm: { span: 18 },
			},
		}

		return <Modal title={this.props.config.title}
		onOk={this.handleOk}
		visible={this.props.config.visible}
		confirmLoading={this.state.loading}
		onCancel={this.props.onCancel}
		okText="提交"
		style={{top: 50, marginBottom: 100}}
		cancelText="取消">
			<Form>
				{this.state.rules.map((item, index) => 
					<FormItem key={index} {...formItemLayout} label={item.label}>
						{getFieldDecorator(item.key, item.option)(item.render())}
					</FormItem>
				)}
			</Form>
		</Modal>
	}
}

export default Form.create()(EquipmentForm)


